/**
 * @todo safeTVColors
 */
export class Application {
    /**
     * 
     */
    public requestedTheme: "Light" | "Dark" = "Light";
    public systemColors = {
        baseLow: "#cccccc",
        baseMediumLow: "#999999",
        baseMedium: "#666666",
        baseMediumHigh: "#333333",
        baseHigh: "#000000",

        altLow: "#ffffff",
        altMediumLow: "#ffffff",
        altMedium: "#ffffff",
        altMediumHigh: "#ffffff",
        altHigh: "#ffffff",

        listLow: "#e5e5e5",
        listMedium: "#cccccc",
        listAccentLow: "#99c9ef",
        listAccentMedium: "#66aee7",
        listAccentHigh: "#4da1e3",

        accentLight3: "#a6d8ff",
        accentLight2: "#76b9ed",
        accentLight1: "#429ce3",
        accent: "#0078d7",
        accentDark1: "#005a9e",
        accentDark2: "#004275",
        accentDark3: "#002642",

        chromeLow: "#f2f2f2",
        chromeMediumLow: "#f2f2f2",
        chromeMedium: "#e6e6e6",
        chromeHigh: "#cccccc",

        chromeAltLow: "#171717",

        chromeDisabledLow: "#7a7a7a",
        chromeDisabledHigh: "#cccccc",

        chromeBlackLow: "#cccccc",
        chromeBlackMediumLow: "#999999",
        chromeBlackMedium: "#333333",
        chromeBlackHigh: "#000000",

        chromeWhite: "#ffffff",
    };
    private systemColorsLists = {
        baseLow: { light: "#cccccc", dark: "#333333" },
        baseMediumLow: { light: "#999999", dark: "#666666" },
        baseMedium: { light: "#666666", dark: "#999999" },
        baseMediumHigh: { light: "#333333", dark: "#cccccc" },
        baseHigh: { light: "#000000", dark: "#ffffff" },
        altLow: { light: "#ffffff", dark: "#000000" },
        altMediumLow: { light: "#ffffff", dark: "#000000" },
        altMedium: { light: "#ffffff", dark: "#000000" },
        altMediumHigh: { light: "#ffffff", dark: "#000000" },
        altHigh: { light: "#ffffff", dark: "#000000" },
        listLow: { light: "#e5e5e5", dark: "#1a1a1a" },
        listMedium: { light: "#cccccc", dark: "#333333" },
        listAccentLow: { light: "#99c9ef", dark: "#004881" },
        listAccentMedium: { light: "#66aee7", dark: "#0060ac" },
        listAccentHigh: { light: "#4da1e3", dark: "#006cc1" },
        accentLight3: { light: "#a6d8ff", dark: "#a6d8ff" },
        accentLight2: { light: "#76b9ed", dark: "#76b9ed" },
        accentLight1: { light: "#429ce3", dark: "#429ce3" },
        accent: { light: "#0078d7", dark: "#0078d7" },
        accentDark1: { light: "#005a9e", dark: "#005a9e" },
        accentDark2: { light: "#004275", dark: "#004275" },
        accentDark3: { light: "#002642", dark: "#002642" },
        chromeLow: { light: "#f2f2f2", dark: "#171717" },
        chromeMediumLow: { light: "#f2f2f2", dark: "#2b2b2b" },
        chromeMedium: { light: "#e6e6e6", dark: "#1f1f1f" },
        chromeHigh: { light: "#cccccc", dark: "#767676" },
        chromeAltLow: { light: "#171717", dark: "#f2f2f2" },
        chromeDisabledLow: { light: "#7a7a7a", dark: "#7a7a7a" },
        chromeDisabledHigh: { light: "#cccccc", dark: "#333333" },
        chromeBlackLow: { light: "#cccccc", dark: "#000000" },
        chromeBlackMediumLow: { light: "#999999", dark: "#000000" },
        chromeBlackMedium: { light: "#333333", dark: "#000000" },
        chromeBlackHigh: { light: "#000000", dark: "#000000" },
        chromeWhite: { light: "#ffffff", dark: "#ffffff" },
    };
    private TVSafeSystemColors = {

    };
    public constructor() {
        this.setTheme(this.requestedTheme);
    }
    public setTheme(theme: "Light" | "Dark") {
        this.requestedTheme = theme;
        const systemColors: any = {};
        for (const key in this.systemColorsLists) {
            if (this.systemColorsLists.hasOwnProperty(key)) {
                systemColors[key] = this.systemColorsLists[key][theme.toLowerCase()];
            }
        }
        this.systemColors = systemColors;
    }
    public setLightTheme() {
        this.setTheme("Light");
    }
    public setDarkTheme() {
        this.setTheme("Dark");
    }
}
export const application = new Application();



