import * as React from "react";
import { TextBlock } from "../TextBlock/TextBlock";
import { application } from "../Core/Application/Application";

export interface ITextBoxProp {
    /**
     * 
     */
    isEnable?: boolean;
    /**
     * 
     */
    style?: React.CSSProperties;
    value?: string;
    /**
     * 
     */
    header?: string;
    /**
     * 
     */
    placeholder?: string;
    /**
     * 
     */
    maxLength?: number;
    /**
     * 
     */
    onChange?: (value: string) => void;
}
export interface ITextBoxState {
    value: string;
}

export class TextBox extends React.Component<ITextBoxProp, ITextBoxState> {
    public state = {
        value: this.props.value ? this.props.value : "",
    };
    private style = require("./style");
    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    // public componentWillMount() {

    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    public componentWillReceiveProps(props) {
        if (props.value) {
            this.setState({
                value: props.value ? props.value : "",
            });
        }
    }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }

    public render() {
        return <div>
            <TextBlock
                style={{
                    color: application.systemColors.baseHigh,
                }}
                text={this.props.header} />
            <input

                value={this.state.value}
                style={{
                    backgroundColor: application.systemColors.altHigh,
                    borderColor: application.systemColors.baseMedium,
                    margin: "5px 0",
                    color: this.props.isEnable ? application.systemColors.baseHigh : application.systemColors.baseLow,
                }}
                disabled={this.props.isEnable ? false : true}
                placeholder={this.props.placeholder}
                onChange={(event) => { this.onChange(event); }}
                className={
                    this.style.input + " " +
                    (this.props.isEnable ? this.style.enable : this.style.disabled)
                } />
        </div>;
    }
    private onChange(event) {
        if (this.props.onChange) {
            this.props.onChange(event.target.value);
        }
        this.setState({
            value: event.target.value,
        });
    }
}



