import * as React from "react";
import { application } from "../Core/Application/Application";
import { TextBlock } from "../TextBlock/TextBlock";
import { IUIElementProp, IUIElementState, UIElement } from "../UIElement/UIElement";

export interface IToggleSwitchProp extends IUIElementProp<void> {
    /**
     * 
     */
    header?: string;
    /**
     * 
     */
    checked?: boolean;
    /**
     *  default is true
     */
    isEnable?: boolean;
    click?: () => Promise<void>;
}
export interface IToggleSwitchState<T> extends IUIElementState<void> {
    check?: boolean;
}
/**
 * 
 *  2 
 * 
 *  
 * 
 * 
 * https://docs.microsoft.com/zh-cn/windows/uwp/controls-and-patterns/toggles
 * <ToggleSwitch x:Name="ToggleSwitch1" header="Button" checked={true} />
 */
export class ToggleSwitch extends UIElement<IToggleSwitchProp, IToggleSwitchState<void>> {
    public state = {
        check: false,
    };
    private style = require("./style");
    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    // public componentWillMount() {

    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {

    // }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }
    public render() {
        return <div className={[
            this.state.check ? this.style.checkBoxOn : this.style.checkBoxOff,
            this.process ? this.style.process : null,
        ].join(" ")}>
            <TextBlock
                style={{
                    color: application.systemColors.baseHigh,
                }}
                text={this.props.header} />
            <div
                className={this.style.inlineBlock}
                onClick={() => { this.onClick(); }}>
                <span
                    style={{
                        borderColor: application.systemColors.chromeAltLow,
                    }}
                    className={this.style.box}>
                    <i
                        style={{
                            backgroundColor: application.systemColors.chromeAltLow,
                        }}
                        className={this.style.boxItem}></i>
                </span>
                <TextBlock
                    text={this.state.check ? "On" : "Off"}
                    style={{
                        color: application.systemColors.baseHigh,
                        display: "inline-block",
                        margin: "0px 10px",
                        height: "20px",
                    }} />
            </div>
        </div>;
    }
    private async onClick() {
        if (typeof this.props.isEnable === "undefined" || this.props.isEnable === true) {
            if (this.props.click) {
                await this.processing(async () => {
                    await this.props.click();
                });
                const check = !this.state.check;
                await this.setState({ check });
            }
        }
    }
}



