import * as React from "react";

/**
 * 
 * value
 */
export interface IProgressBarProp {
    /**
     * 
     */
    value?: number;
    /**
     * 
     * @TODO
     */
    color?: string;
    /**
     * 0
     */
    minimum?: number;
    /**
     * 100
     */
    maximum?: number;
    /**
     * 
     */
    style?: React.CSSProperties;
    /**
     * 
     */
    isIndeterminate?: boolean;
}
export interface IProgressBarState {
    none?: any;
}
export class ProgressBar extends React.Component<IProgressBarProp, IProgressBarState> {
    private style = require("./style");
    public render() {
        return <div
            className={this.style.box}
            style={this.props.style}
        >
            <progress

                value={this.props.value}
                max={this.props.maximum ? this.props.maximum : 100}
                className={this.style.progress}>progress</progress>
        </div>;
    }
}



