import { dateUtil } from "@kemao-node-basis/lark/browser/utils/DateUtil";
import * as React from "react";
import { Button } from "../../ui/Button/Button";
import { application } from "../../ui/Core/Application/Application";
import { HyperlinkButton } from "../../ui/HyperlinkButton/HyperlinkButton";
import { ReactImage } from "../../ui/Image/Image";
import { ProgressBar } from "../../ui/ProgressBar/ProgressBar";
import { ProgressRing } from "../../ui/ProgressBar/ProgressRing";
import { TextBox } from "../../ui/TextBox/TextBox";
import { ToggleSwitch } from "../../ui/ToggleSwitch/ToggleSwitch";
import { Base } from "../../ui/Typography/Base";
import { BaseAlt } from "../../ui/Typography/BaseAlt";
import { Body } from "../../ui/Typography/Body";
import { Caption } from "../../ui/Typography/Caption";
import { CaptionAlt } from "../../ui/Typography/CaptionAlt";
import { Header } from "../../ui/Typography/Header";
import { Subheader } from "../../ui/Typography/Subheader";
import { Subtitle } from "../../ui/Typography/Subtitle";
import { SubtitleAlt } from "../../ui/Typography/SubtitleAlt";
import { Title } from "../../ui/Typography/Title";
export class MetroIndex extends React.Component<null, null> {
    // public state = {}
    // public constructor(props) {
    //    super(props);
    // }
    /**
     * 
     */
    // public componentWillMount() {
    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {
    // }
    // public shouldComponentUpdate() {
    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {
    // }
    /**
     * 
     */
    // public componentWillUnmount() {
    // }
    public render() {
        return <div
            style={{
                backgroundColor: application.systemColors.altHigh,
            }}
        >
            <Header>Header</Header>
            <Subheader>Subheader</Subheader>

            <Title>Title</Title>
            <Subtitle>Subtitle</Subtitle>
            <SubtitleAlt>SubtitleAlt</SubtitleAlt>

            <Base>Base</Base>
            <BaseAlt>BaseAlt</BaseAlt>
            <Body>Body</Body>

            <Caption>Caption</Caption>
            <CaptionAlt>CaptionAlt</CaptionAlt>

            <Subheader>HyperlinkButton</Subheader>
            <Body>
                
                <HyperlinkButton
                    click={() => {
                        alert("hi~");
                    }}
                    content="" />
                
                <HyperlinkButton content="hyperlinkButton" />dfafa
                <HyperlinkButton isEnable={false} content="" />
            </Body>

            <Subheader>ToggleSwitch</Subheader>
            <ToggleSwitch header="" isEnable={true} click={async () => {
                throw new Error("");
            }} />
            <ToggleSwitch header="" isEnable={true} click={async () => {
                {/* const util = new DateUtil(); */ }
                await dateUtil.delay(11100);
            }} />
            <ToggleSwitch header="" click={async () => {
                {/* const util = new DateUtil(); */ }
                await dateUtil.delay(0);
            }} />
            <hr />

            <Subheader>Button</Subheader>
            <Button content="1"
                click={async () => {
                    await dateUtil.delay(10 * 1000);
                }}
                isEnable={true} />
            <Button content="2"
                click={async () => {
                }}
                isEnable={false} />
            <Button content="3"
                click={async () => {
                }} />
            <Button content=""
                click={async () => {
                    await dateUtil.delay(10 * 1000);
                }}
                isEnable={true} />
            <Button content=""
                click={async () => {
                    await dateUtil.delay(10 * 1000);
                }}
                isEnable={true} />
            <Button content="" isEnable={true} />

            <Subheader>Image</Subheader>
            <ReactImage src="https://img5.mtime.cn/mg/2017/09/26/092714.54558312.jpg" />

            <Subheader>TextBox</Subheader>
            <TextBox
                header="url:"
                isEnable={true}
                placeholder=""
                style={{ margin: "10px 0" }}
            />
            <TextBox
                value="TextBox"
                isEnable={true}
                style={{ margin: "10px 0" }}
            />
            <TextBox
                value="TextBox"
                isEnable={true}
                onChange={(value) => {
                    console.log("TextBox.onCahge", value);
                }}
                style={{ margin: "10px 0" }}
            />
            <TextBox
                value="TextBox"
                style={{ margin: "10px 0" }}
            />

            <Subheader>ProgressBar</Subheader>
            <ProgressBar
                value={10}
                style={{ margin: "20px 0" }}
            />
            <ProgressBar
                value={30}
                style={{ margin: "20px 0" }} />
            <ProgressBar
                value={50}
                style={{ margin: "20px 0" }} />
            <ProgressBar
                value={100}
                style={{ margin: "20px 0" }} />

            <ProgressRing
                style={{
                    height: 32,
                    width: 32,
                }}
            />
            <ProgressRing
                style={{
                    height: 48,
                    width: 48,
                }}
            />
            <ProgressRing
                style={{
                    height: 64,
                    width: 64,
                }}
            />
        </div >;
    }
}



