import * as React from "react";
import { IUIElementProp, IUIElementState, UIElement } from "../UIElement/UIElement";
import { ProgressRing } from "../ProgressBar/ProgressRing";
import { application } from "../Core/Application/Application";

export interface IButtonProp {
    /**
     * 
     */
    content: string;
    toolTip?: string;
    /**
     * 
     * true
     */
    isEnable?: boolean;
    style?: React.CSSProperties;
    /**
     * 
     */
    click?: () => Promise<void>;
}
export interface IButtonState {
}

export class Button extends UIElement<IButtonProp, IButtonState> {
    private style = require("./style");
    // public state = {}

    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    // public componentWillMount() {

    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {

    // }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }

    public render() {
        return <button
            style={{
                backgroundColor: application.systemColors.baseLow,
                color: application.systemColors.baseHigh,
                borderColor: application.systemColors.baseLow,
            }}
            onClick={() => { this.onClick(); }}
            className={[
                this.process ? this.style.process : null,
                this.style.button,
                (this.props.isEnable === false ? this.style.disabled : this.style.enable),
            ].join(" ")}
        >{this.process ? <ProgressRing
            style={{
                height: 16,
                width: 16,
            }}
        /> : this.props.content}</button>;
    }
    private async onClick() {
        if ((this.props.isEnable === false) !== true) {
            if (this.props.click) {
                await this.processing(async () => {
                    await this.props.click();
                });
            }
        }
    }
}



