import * as React from "react";
import { ProgressRing } from "../ProgressBar/ProgressRing";
import { application } from "../Core/Application/Application";
import { HyperlinkButton } from "../HyperlinkButton/HyperlinkButton";
import { dateUtil } from "@kemao-node-basis/lark/browser/utils/DateUtil";
export interface IImageProp {
    src: string;
}
export interface IImageState {
    loading: boolean;
    error: boolean;
}
export class ReactImage extends React.Component<IImageProp, IImageState> {
    public state = {
        loading: true,
        error: false,
    };
    private style = require("./style");
    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    public async componentWillMount() {

        this.initImage();
    }

    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {

    // }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }
    //
    public render() {
        return <div
            className={this.style.box}
            style={{
                backgroundColor: this.state.loading === false ? application.systemColors.chromeWhite : application.systemColors.baseLow,
            }}
        >
            {this.state.error ? <HyperlinkButton content="" click={async () => {
                await this.initImage();
            }} /> : this.state.loading ? <ProgressRing style={{
                width: 32,
                height: 32,
                margin: 10,
            }} /> : <img className={this.style.img} src={this.props.src} />}
        </div>;
    }
    public async setState(state) {
        return new Promise((resolve, reject) => {
            super.setState(state, () => {
                resolve();
            });
        });
    }
    private async initImage() {
        await this.setState({ loading: true, error: false });
        await dateUtil.delay(2000);
        const image = new Image();
        image.addEventListener("load", async () => {
            await this.setState({ loading: false });
        });
        image.addEventListener("error", async () => {
            await this.setState({ error: true });
        });
        image.src = this.props.src;
    }
}



