import { application } from "../Core/Application/Application";
import * as React from "react";

export interface IHyperlinkButtonProp {
    content: string;
    navigateUri?: string;
    click?: () => void;
    /**
     * default true
     */
    isEnable?: boolean;
}
export interface IHyperlinkButtonState {
}
// <HyperlinkButton Content="www.microsoft.com"
//                 NavigateUri="http://www.microsoft.com"/>
export class HyperlinkButton extends React.Component<IHyperlinkButtonProp, IHyperlinkButtonState> {
    private style = require("./style");
    // public state = {}

    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    // public componentWillMount() {

    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {

    // }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }

    public render() {
        return <a
            href={this.props.navigateUri}
            className={[
                this.style.a,
                this.props.isEnable === false ? this.style.disabled : this.style.enable,
            ].join(" ")}
            onClick={() => { this.onClick(); }}
            style={{
                color: this.props.isEnable === false ? application.systemColors.baseLow : application.systemColors.accent,
            }}
        >{this.props.content}</a>;
    }
    private onClick() {
        if (this.props.click) {
            this.props.click();
        }
    }
}



