import * as React from "react";
import * as ReactDom from "react-dom";

/**
 * react
 */
export class ReactHelper {
    /**
     * 
     */
    public render(component, props: any = {}, children = null) {
        this.renderElement(React.createElement(component, props, children));
    }
    public renderElement(element) {
        const container = document.createElement("div");
        document.body.appendChild(container);
        ReactDom.render(element, container);
    }
}

export const reactHelper = new ReactHelper();



