import * as React from "react";

/**
 * 
 * https://msdn.microsoft.com/zh-cn/library/windows/apps/windows.ui.xaml.controls.progressring
 */
export interface IProgressRingProp {
    /**
     * true
     */
    isActive?: boolean;
    style?: React.CSSProperties;
}
export interface IProgressRingState {
    none?: string;
}

export class ProgressRing extends React.Component<IProgressRingProp, IProgressRingState> {
    private images = require("./images");
    private style = require("./style");
    // public state = {}

    // public constructor(props) {
    //    super(props);
    // }

    /**
     * 
     */
    // public componentWillMount() {

    // }
    /**
     * 
     */
    // public componentDidMount() {
    // }
    /**
     * props
     * @param props props
     */
    // public componentWillReceiveProps(props) {

    // }
    // public shouldComponentUpdate() {

    // }
    // public componentWillUpdate() {

    // }
    // public componentDidUpdate() {

    // }
    /**
     * 
     */
    // public componentWillUnmount() {

    // }

    public render() {
        const active = typeof this.props.isActive === "boolean" ? this.props.isActive : true;
        if (active === false) {
            return null;
        }
        return <div
            className={this.style.box}
        >
            <div style={this.props.style} className={this.style.loader + " " + this.images.loader}></div>
        </div>;
    }
}



